h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0; }

body {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 700;
  font-size: 90%;
  line-height: 1.2;
  background-color: #fff;
  letter-spacing: 0.02em; }
  @media (min-width: 32em) {
    body {
      font-size: 103%; } }
  @media (min-width: 54em) {
    body {
      font-size: 105%; } }
  @media (min-width: 65em) {
    body {
      font-size: 107.5%; } }
  @media (min-width: 75em) {
    body {
      font-size: 110%; } }
  @media (min-width: 91em) {
    body {
      font-size: 115%; } }
  @media (min-width: 115em) {
    body {
      font-size: 120%; } }
  @media (min-width: 130em) {
    body {
      font-size: 125%; } }

p {
  margin-top: 0; }
  p:last-of-type {
    margin-bottom: 0; }

ul, ol {
  margin: 0; }

div.page {
  position: relative;
  height: 2500px;
  width: 2500px; }
  div.page section {
    position: absolute; }
  div.page section.splash {
    position: absolute;
    top: 50%;
    left: 0;
    text-transform: uppercase;
    text-align: center;
    width: 100%;
    transform: translateY(-50%); }
    div.page section.splash img {
      margin-top: .2em;
      width: 4em;
      height: auto;
      pointer-events: none; }
    div.page section.splash .like {
      transition: all .1s ease;
      cursor: pointer; }
      div.page section.splash .like:hover {
        text-decoration: underline; }
      div.page section.splash .like.active {
        color: #5890ff; }
  div.page section.chat {
    top: 53%;
    right: 33%;
    background: white; }
  div.page section.coffee {
    text-align: center;
    top: 53%;
    right: 36%; }
    div.page section.coffee img {
      width: 10em;
      height: auto; }
  div.page section.empathy {
    left: 20%;
    top: 25%;
    width: 40em; }
    div.page section.empathy img {
      width: 100%; }
  div.page section.lifestyle-attributes {
    left: 4%;
    top: 7%;
    width: 32em; }
  div.page section.profiles {
    right: 15%;
    top: 50%;
    text-transform: uppercase; }
    div.page section.profiles .words {
      height: 4em;
      width: 22em;
      padding: 0 1em;
      display: flex;
      align-items: center;
      background: black;
      color: white; }
    div.page section.profiles .profiles__word {
      width: 50%;
      display: inline-block;
      text-align: center;
      vertical-align: middle; }
    div.page section.profiles .adj {
      margin-right: 1em; }
  div.page section.friendly {
    right: 32%;
    top: 38%;
    -webkit-perspective: 800px;
    padding: 0;
    background: none;
    width: 12em;
    height: 4.5em;
    cursor: pointer; }
    div.page section.friendly .card {
      transform-style: preserve-3d;
      transition: 0.5s;
      backface-visibility: hidden; }
      div.page section.friendly .card.flip {
        transform: rotatex(-180deg); }
    div.page section.friendly > div {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 2;
      text-align: center; }
    div.page section.friendly .face {
      width: 12em;
      height: 4.5em;
      position: absolute;
      padding: 0 1em;
      background: white;
      display: flex;
      justify-content: center;
      flex-direction: column; }
    div.page section.friendly .back {
      transform: rotatex(-180deg);
      backface-visibility: hidden; }
  div.page section.running {
    left: 20%;
    bottom: 37%; }
  div.page section.pokemon {
    right: 5%;
    top: 20%;
    font-size: .8em;
    display: flex;
    align-items: center; }
    div.page section.pokemon img {
      width: 16em;
      height: auto;
      margin-right: 1em; }
    div.page section.pokemon .paper {
      max-width: 50em; }

.point {
  font-size: 1.4em;
  font-style: italic; }

.paper {
  background: white;
  padding: 2em;
  border-radius: 5px;
  border: solid 1px rgba(0, 0, 0, 0.07);
  box-shadow: 0px 0px 46px 0px rgba(0, 0, 0, 0.2); }

.hover, div.page section.chat {
  border-radius: 5px;
  box-shadow: 0px 0px 46px 0px rgba(0, 0, 0, 0.2); }

.dragscroll {
  overflow: scroll;
  cursor: move;
  cursor: -moz-grab;
  cursor: -o-grab;
  cursor: grab;
  cursor: -webkit-grab; }
  .dragscroll:active {
    cursor: move;
    cursor: -moz-grabbing;
    cursor: -o-grabbing;
    cursor: grabbing;
    cursor: -webkit-grabbing; }

h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 2.75em; }

div.viewport {
  z-index: 1;
  position: relative; }

div.left {
  position: fixed;
  bottom: 1.5em;
  left: 1.5em; }
  div.left span.centerViewport {
    opacity: .2;
    cursor: pointer; }
    div.left span.centerViewport:hover {
      opacity: 1; }

div.right {
  position: fixed;
  bottom: 1.5em;
  right: 1.5em; }
  div.right span, div.right a {
    display: block;
    font-size: 0.9em; }
